import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class TentangSaya extends Component {
  render() {
    return (
      <div
        className='content-body'
        style={{
          overflow: 'auto',
          height: 'inherit'
        }}
      >
        <Grid className='content-grid2'>
          <Cell col={3}>
            <div className='content-list'>
              <img
                src='foto animasi.jpg'
                alt='avatar'
              />
              <h4>Alvian</h4>
              <hr style={{ borderTop: '3px solid #833fb2' }} />
              <center>
                <p>Informasi tentang Alvian</p>
                <p>Its Me</p>
                <p>
                  Email:{' '}
                  <a
                    href='mailto:ahmadhasimy@mail.com?Subject=Hello%20Node.js%20Programmer!'
                    target='_top'
                  >
                    alalvian200@gmail.com
                  </a>
                </p>
                <p>
                  Github:{' '}
                  <a
                    href='https://nodejs.org/en/about'
                    rel='noopener noreferrer'
                    target='_blank'
                  >
                    https://github.com/hasimy-as
                  </a>
                </p>
              </center>
            </div>
          </Cell>

          <Cell
            className='resume-right-col'
            col={9}
            style={{ marginBottom: '90px' }}
          >
            <h4>Riwayat</h4>
            <Grid>
              <Cell col={4}>
                <p>Oktober 2021</p>
              </Cell>
              <Cell col={8}>
                <h5 style={{ marginTop: '0px' }}>
                  Saya mulai menggunakan CodeIgniter.
                </h5>
              </Cell>
            </Grid>
            <Grid>
              <Cell col={4}>
                <p>Sekarang</p>
              </Cell>
              <Cell col={8}>
                <h5 style={{ marginTop: '0px' }}>
                  
                  Saya mulai Menggunakan beberapa framework seperti Laravel,ReacJs DLL 
                </h5>
              </Cell>
            </Grid>
            <hr style={{ borderTop: '3px solid #e22947' }} />
            <h4>Penggunaan Bahasa Pemrograman</h4>
            <Grid>
              <Cell col={4}>
                <p>Agustus 2020 - Oktober 2021 </p>
              </Cell>
              <Cell col={8}>
                <h5 style={{ marginTop: '0px' }}>HTML,CSS,JS</h5>
              </Cell>
              <Cell col={4}>
                <p>Oktober 2021 - Agustus 2022</p>
              </Cell>
              <Cell col={8}>
                <h5 style={{ marginTop: '0px' }}>PHP Native, CodeIgniter</h5>
              </Cell>
              <Cell col={4}>
                <p>Februari 2022- Sekarang</p>
              </Cell>
              <Cell col={8}>
                <h5 style={{ marginTop: '0px' }}>
                 Laravel,ReactJs,VueJs
                </h5>
              </Cell>
            </Grid>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default TentangSaya;



