import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class Beranda extends Component {
  render() {
    return (
      <div style={{ width: '100%', margin: 'auto' }}>
        <Grid className='beranda-grid'>
          <Cell col={12}>
            <img
               alt="avatar"
               width={"100px"}
              src='foto animasi.jpg'
              className='avatar-img'
            />

            <div className='banner-text'>
              <h1>Alvian</h1>
              <hr />
              <p>
                Saya adalah programmer yang bergerak dalam bidang
                Web Developer.<br></br>Saya pengguna reactJs,VueJs,Laravel,CodeIgniter.
              </p>
              <div className='social-links'>
                <a
                  href='https://www.instagram.com/alvifebrn/'
                  rel='noopener noreferrer'
                  target='_blank'
                >
                  <i
                    className='fa fa-instagram fa-3x social'
                    aria-hidden='true'
                  />
                </a>
                <a
                  href='https://github.com/AlvianAhmad'
                  rel='noopener noreferrer'
                  target='_blank'
                >
                  
                  <i className='fa fa-github fa-3x social' aria-hidden='true' />
                </a>
                <a
                  href='https://www.linkedin.com/in/alvian-febrian-55680926a'
                  rel='noopener noreferrer'
                  target='_blank'
                >
                  <i
                    className='fa fa-linkedin fa-3x social'
                    aria-hidden='true'
                  />
                </a>
              </div>
            </div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Beranda;



